.fill {
	width: 310px;
	height: 100%;
	box-sizing: content-box;
	/* filter: opacity(25%); */
}

.fill .on {
	filter: opacity(25%);
}

.card-card .card-overlay {
	user-select: none;
	position: absolute;
	opacity: 0.6;


	background-color: #ffffff;
	transition: all 300ms ease;
	border-radius: 15px;
}

.card-card:hover .card-overlay {
	/* background-color: #0000002d; */
}

.react-stacked-center-carousel-slide-0 .card-overlay {
	/* background-color: #00000099 !important; */
}

.card-card .cover-image {
	object-fit: contain;
}

.card-card .cover {
	position: absolute;
	transition: opacity 300ms ease;
}

.react-stacked-center-carousel-slide-0 .card-card .cover {
	transition: opacity 300ms ease, z-index 0ms 300ms;
}

.card-card .cover.on {

	opacity: 1;
	z-index: 1;
}

.card-card .cover.off {
	opacity: 0;
	z-index: -1;
}

